<template>
    <div class="box" v-if="infoArr.length>0">
        <h3>{{infoArr[0].title}}</h3>
        <div class="left">
            <div class="rate">
                <van-rate v-model="infoArr[0].rating.average" allow-half void-icon="star" void-color="#eee" size="0.15rem" readonly/>
                {{infoArr[0].rating.average*2}}<span>{{infoArr[0].rating.details[3]}}人评分</span>
            </div>
            <p>
                {{infoArr[0].year}}&nbsp;/
                <span v-for="(genres,gen) in infoArr[0].genres" :key="gen+'_gen'">&nbsp;{{genres}}&nbsp;/</span>
                <span v-for="(casts,cas) in infoArr[0].casts" :key="'cas_'+cas">&nbsp;{{casts.name}}&nbsp;/</span>
                <span>&nbsp;{{infoArr[0].pubdates[1]||infoArr[0].pubdates[0]|dizhi}}</span>
            </p>
            <a href="#">用APP查看影人资料</a>
        </div>
        <div class="right">
            <img :src="infoArr[0].images.small">
        </div>
        <div class="btn">
            <a href="#">想看</a>
            <a href="#">看过</a>
        </div>
        <div class="synopsis">
            <h4>{{infoArr[0].title}}的简介</h4>
            <p>
                {{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介
                <span v-if="synopsis">··· ···<b @click="show()">[展开]</b></span>
                <span v-else>{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介{{infoArr[0].title}}的简介<b @click="hide()">[收起]</b></span>
            </p>
        </div>
        <div class="kind">
            <h4>查看更多分类</h4>
            <span v-for="(genres,gen) in infoArr[0].genres" :key="gen+'_gen'">&nbsp;{{genres}}</span>
        </div>
        <div class="img">
            <h4>{{infoArr[0].title}}剧照</h4>
            <li>
                <img :src="infoArr[0].images.small">
                <img v-for="(casts,cas) in infoArr[0].casts" :key="'cas_'+cas"  :src="casts.avatars.small">
            </li>
        </div>
        <div class="comment">
            <h4>{{infoArr[0].title}}电影短评</h4>
            <li v-for="(comment,com) in comment" :key="'com_'+com">
                <div class="top">
                    <img src="images/ic_douban_pay.png">
                    <p>
                        <span>豆瓣</span>
                        <span>{{comment.times}}</span>
                    </p>
                </div>
                <div class="bottom">
                    <p>{{comment.text}}</p>
                    <p>
                        <span><van-icon name="good-job-o" size="0.2rem"/><b>{{comment.num}}</b></span>
                        <span>···</span>
                    </p>
                </div>
            </li>
            <a href="#">显示更多评论</a>
        </div>
        <div class="question">
            <h4>{{infoArr[0].title}}的问答</h4>
            <li v-for="(question,que) in question" :key="'que_'+que">
                <span>{{question.question}}</span>
                <span>{{question.num}}回答</span>
            </li>
            <a href="#">显示更多问答</a>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            synopsis:true,
            comment:[],
            question:[]
        }
    },
    props:["infoArr"],
    filters:{
        dizhi(val){
            var i = val.indexOf("(");
            var j = val.indexOf(")")
            return val.slice(i+1,j)
        }
    },
    methods: {
        show(){
            this.synopsis = !this.synopsis
        },
        hide(){
            this.synopsis = !this.synopsis
        }
    },
    mounted() {
        this.axios({
            url:"/api/movie/comment",
            method:"get"
        }).then(res=>{
            this.comment = res.data.data
        });
        this.axios({
            url:"/api/movie/question",
            method:"get"
        }).then(res=>{
            this.question = res.data.data
        })
    },
}
</script>
<style scoped>
    b{
        font-weight: normal;
    }
    li{
        list-style: none;
    }
    .box{
        width: 3rem;
        margin: 0 auto;
        font-size: 0.12rem;
    }
    .box h3{
        margin-top: 0.2rem;
        margin-bottom: 0.05rem;
    }
    .left{
        float: left;
        width: 2.3rem;
        height: 1.3rem;
    }
    .rate{
        display: flex;
        font-size: 0.14rem;
        align-items: center;
        color: #cccccc;
    }
    .rate>span{
        margin-left: 0.15rem;
    }
    .left>p{
        font-size:0.14rem;
        color: #333333;
        margin-right: 0.1rem;
    }
    .left>a{
        margin-top: 0.3rem;
        display: inline-block;
        color: green;
        font-size: 0.12rem;
    }
    .right{
        float: right;
        width: 0.7rem;
        height: 1.3rem;
    }
    .right img{
        width: 0.7rem;
    }
    .btn{
        width: 100%;
        height: 0.25rem;
        display: flex;
        justify-content: space-between;
    }
    .btn a{
        box-sizing: border-box;
        display: inline-block;
        width: 48%;
        height: 100%;
        line-height: 0.25rem;
        font-size: 0.12rem;
        color: orangered;
        border: 1px solid orangered;
        border-radius: 0.05rem;
        text-align: center;
    }
    .synopsis{
        width: 100%;
        margin: 0.2rem 0;
    }
    .synopsis b{
        color: green;
    }
    .box>div{
        margin-bottom: 0.2rem;
    }
    .box>div h4{
        color: #cccccc;
        font-size: 0.14rem;
        font-weight: normal;
        margin-bottom: 0.2rem;
    }
    .box .kind span{
        display: inline-block;
        width: 0.5rem;
        height: 0.2rem;
        background-color: #cccccc;
        font-size: 0.14rem;
        line-height: 0.2rem;
        text-align: center;
        margin-right: 0.15rem;
        border-radius: 0.1rem;
    }
    .box .img{
        width: 100%;
        overflow-x: auto;
    }
    .box .img li{
        width: 100%;
        display: flex;
    }
    .box .img img{
        width: 1.2rem;
        margin-right: 0.1rem;
    }
    .comment{
        width: 100%;
    }
    .comment li{
        padding-bottom: 0.1rem;
        border-bottom: 1px solid #cccccc;
        margin-top: 0.15rem;
    }
    .comment .top{
        width: 100%;
        display: flex;
    }
    .comment .top img{
        width: 0.4rem;
        height: 0.4rem;
        border-radius: 50%;
        margin-right: 0.2rem;
    }
    .comment .top p{
        display: flex;
        flex-flow: column;
        line-height: 0.20rem;
    }
    .comment .top p span:first-child{
        font-weight: bold;
        font-size: 0.14rem;
    }
    .comment .top p span:last-child{
        font-size: 0.12rem;
        color: #cccccc;
    }
    .comment .bottom{
        box-sizing: border-box;
        padding-left: 0.6rem;
        width: 100%;
    }
    .comment .bottom p:first-child{
        height: 0.8rem;
        font-size: 0.14rem;
        line-height: 0.16rem;
        margin-top: 0.15rem;
    }
    .comment .bottom p:last-child{
        display: flex;
        justify-content: space-between;
    }
    .comment .bottom p:last-child span:first-child{
        display: flex;
        align-items: center;
        font-size: 0.12rem;
    }
    .comment .bottom p:last-child span:first-child b{
        position: relative;
        top: 0.04rem;
        margin-left: 0.05rem;
    }
    .comment .bottom p:last-child span:last-child{
        font-size: 0.18rem;
        font-weight: bold;
    }
    .comment>a{
        font-size: 0.14rem;
        color: green;
        display: block;
        width: 100%;
        text-align: center;
        margin-top: 0.2rem;
    }
    .question{
        width: 100%;
    }
    .question li{
        width: 100%;
        height: 0.5rem;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        margin-bottom: 0.15rem;
    }
    .question li span:first-child{
        font-size: 0.14rem;
    }
    .question li span:last-child{
        font-size: 0.12rem;
        color: green;
    }
    .question>a{
        font-size: 0.14rem;
        color: green;
        display: block;
        width: 100%;
        text-align: center;
        margin-top: 0.2rem;
    }
</style>