<template>
    <div class="box">
        <div v-if="title=='发现好电影'||'推荐的豆列'">
            <h3  @click="fxhdy()" v-zclick>{{title}}<a href="#">更多</a></h3>
            <div>
                <ul>
                    <li v-for="(v,i) in this.$store.state.fxhdyArr" :key="i" :style="{color:v.color}">{{v.title}}</li>
                </ul>
            </div>
        </div>
        <div v-else>
            <h3  @click="goodBook" v-zclick>{{title}}<a href="#">更多</a></h3>
            <div>
                <ul>
                    <li v-for="(v,i) in this.$store.state.goodBook" :key="i" :style="{color:v.color}">{{v.title}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:["title"],
    methods:{
        fxhdy(){
            this.$store.dispatch("ajaxData")
        },
        goodBook(){
            this.$store.dispatch("goodBookData")
        },
    },
    directives:{
        zclick:{
            inserted(el){
                el.click()
            }
        }
    }
}
</script>
<style scoped>
   .box{
       width: 3rem;
       margin: 0 auto;
       font-size: 0.12rem;
   }
   .box h3{
        font-size: 0.14rem;
        color: #333333;
        height: 0.4rem;
        line-height: 0.4rem;
        font-weight: normal;
    }
    .box h3 a{
        color: green;
        font-size: 0.12rem;
        float: right;
        height: 0.4rem;
        line-height: 0.4rem;
        text-decoration: none;
    }
    .box>div{
        width: 100%;
       overflow-x: auto;
    }
    ul{
        display: flex;
        height: 1rem;
        width: 8rem;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    ul li{
        height: 0.20rem;
        padding: 0.1rem 0.15rem;
        border: 1px solid orangered;
        border-radius: 0.05rem;
        font-size: 0.12rem;
    }
</style>