<template>
    <div>
        <TopLink></TopLink>
        <div class="clear"></div>
        <img src="images/timg.gif" v-if="dataArr.length==0" class="loding">
        <div v-else>
            <MovieInfo :infoArr="dataArr"></MovieInfo>
            <Fxhdy title="推荐的豆列"></Fxhdy>
        </div>
    </div>
</template>
<script>
import TopLink from "@/components/toplink/toplink.vue"
import eventBus from "@/bus/bus.js"
import MovieInfo from "@/pages/xiangqing/movieInfo/movieInfo.vue"
import Fxhdy from "@/pages/movie/fxhdy/fxhdy.vue"
export default {
    data() {
        return {
            dataArr:[],
        }
    },
    components:{
        TopLink,MovieInfo,Fxhdy
    },
    mounted() {
        eventBus.$on("postData",(res)=>{
            this.dataArr = res;
        });
        var that = this
        setTimeout(function () {
            if(that.dataArr.length<=0){
                var id = window.localStorage.getItem("id");
                if(id){
                    that.axios({
                        url:"/api/movie/xiangqing",
                        method:"get",
                        params:{id:id}
                    }).then(ok=>{
                        that.dataArr = ok.data.data;
                        that.dataArr[0].rating.average = that.dataArr[0].rating.average/2
                    });
                }else{
                    return
                }
            }else{
                return
            }
        }, 500)
    },
}
</script>
<style scoped>
    .loding{
        width: 100%;
    }
</style>